@import url('https://fonts.googleapis.com/css?family=Nunito+Sans:300,800&display=swap');

* {
    box-sizing: border-box;
    font-family: 'Nunito Sans', sans-serif;
    outline: none;
}
  .ppl_favr {
    background: #fff;
    padding: 80px 0;
}
.pfv_title {
    text-align: center;
    margin-bottom: 50px;
}
.pfv_title h2 {
    color: #222;
    font-size: 33px;
    font-weight: 800;
    margin-bottom: 20px;
}
.pfv_title h4 {
    line-height: 30px;
    color: #222;
    font-weight: 300;
    margin-top: 20px;
    font-size: 18px;
}
.pfv_ct a h3 {
    color: #101214;
    font-weight: 800;
    font-size: 22px;
}
.pfv_img {
    position: relative;
    overflow: hidden;
}
.pfv_img>img {
    transition: all .3s ease-in-out;
}
.pfv_img::after {
    content: "";
    display: block;
    width: 0;
    height: 200%;
    position: absolute;
    bottom: 0;
    background: rgba(255,255,255,.7);
    left: -30%;
    box-shadow: 0 0 9px 20px rgba(255,255,255,.1);
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    transition: all .7s ease-in-out;
    top: -36%;
}
.pfv_img:hover::after {
    left: 150%;
}
.pfv_img:hover>img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.img-responsive, .thumbnail a>img, .thumbnail>img {
    display: block;
    max-width: 100%;
    height: auto;
}
