  .fliter_check input[type=checkbox] {
    position: absolute;
    opacity: 0;
  }
  .fliter_check input[type=checkbox]+label::before {
    content: "";
    display: inline-block;
    width: 13px;
    height: 13px;
    border: 1px solid #d5d5d5;
    border-radius: 2px;
    margin-right: 7px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    position: relative;
    top: 2px;
  }
  .fliter_check input[type=checkbox]:checked+label::before {
    background: #2ac884;
    border-color: #2ac884;
  }
  .fliter_check input[type=checkbox]+label {
    cursor: pointer;
    margin: 6px 0;
