.hs1_conte {
  width: 90vw !important;
}
.button {
  background: linear-gradient(90deg, #1bd472, #17a298);
  color: #fff;
  padding: 15px 30px;
  border: 0;
  font-size: 15px;
  font-weight: 800 !important;
  border-radius: 50px;
  text-transform: uppercase;
  outline: none !important;
}
.button:hover {
  color: #fff;
}
.button:focus {
  box-shadow: none !important;
}
.head {
  color: #172234;
  font-weight: 600;
  font-size: 40px !important;
  font-family: avenir, sans-serif;
  margin-top: 40px;
  margin-bottom: 22px;
  line-height: 22px;
}
.heading {
  color: #172234;
  font-weight: 600;
  font-size: 40px;
  font-family: avenir, sans-serif;
  margin-top: 40px;
  margin-bottom: 22px;
  line-height: 22px;
}
.hrp {
  font-size: 18px;
  color: #060606;
  font-weight: 200;
  margin-bottom: 55px;
  line-height: 34px;
  font-family: sans-serif, nunito-sans;
  margin: 0 0 50px;
}
.hs2_conte {
  width: 95%;
  word-wrap: normal;
  margin-top: 18vh !important;
}
.hs2_px {
  position: relative;
  min-height: 1px;
  padding-left: 7.5px;
  padding-right: 7.5px;
}
.hs2_px img {
  position: relative;
  left: 2vw !important;
  top: 5vh;
  display: block;
  max-width: 90%;
  height: auto;
}
.hs2-dot-img {
  display: block;
  width: 300px;
  height: 300px;
  background: url("https://www.payforworks.com/images/qldot.png") no-repeat
    transparent;
  position: absolute;
  left: -19px;
  z-index: -1;
  top: -44px;
}

@media screen and (max-width: 1200px) {
  .hs2_conte .head,
  .hs2_conte .heading {
    font-size: 32px !important;
    margin-top: 28px;
    margin-bottom: -5px !important;
  }
}

@media screen and (max-width: 600px) {
  .hs2_conte .head,
  .hs2_conte .heading {
    font-size: 28px !important;
    margin-top: 0;
    margin-bottom: 0 !important;
    line-height: 46px;
  }
}

@media screen and (max-width: 480px) {
  .hs2_conte .head,
  .hs2_conte .heading {
    font-size: 23px !important;
    line-height: 30px;
  }
}

@media screen and (max-width: 600px) {
  p {
    margin-top: 30px !important;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 1200px) {
  .hs2-dot-img {
    width: 180px;
    height: 180px;
    left: -32px;
    top: -15px;
  }
}
@media screen and (max-width: 992px) {
  .hs2-dot-img {
    left: 0;
    top: 0;
  }
}
@media screen and (max-width: 992px) {
  .hs2_conte {
    position: relative;
    left: -5.5vw;
  }
}

@media screen and (max-width: 600px) {
  .hs2-dot-img {
    width: 110px;
    height: 110px;
  }
}
@media screen and (max-width: 1360px) {
  .hs2_conte {
    position: relative;
    left: -20px;
    top: 4vh;
  }
}
@media screen and (max-width: 1200px) {
  .hs2_conte {
    position: relative;
    left: -34px;
    top: -10vh;
  }
}
@media screen and (max-width: 992px) {
  .hs2_conte {
    position: relative;
    left: -54px;
    top: -10vh;
  }
}
@media screen and (max-width: 600px) {
  .hs2_conte {
    position: relative;
    left: -53px;
    top: -15vh;
  }
}

@media screen and (max-width: 600px) {
  .button {
    height: 45px !important;
    padding: 0 30px;
    line-height: 45px;
    display: inline-block;
    border: 0;
    font-size: 15px;
    font-weight: 800 !important;
    border-radius: 50px;
  }
}

@media screen and (max-width: 480px) {
  .hs2_conte {
    position: relative;
    left: -53px;
    top: -4vh;
  }
}

@media screen and (max-width: 992px) {
  .hs2-dot2-img {
    display: block;
    width: 180px;
    height: 180px;
    background: url("https://www.payforworks.com/images/qldot.png") no-repeat
      transparent;
    position: absolute;
    right: 0;
    z-index: -1;
    bottom: -115px;
    opacity: 0.4;
  }
}

@media screen and (max-width: 992px) {
  .hs2_px img {
    width: 595px;
    right: auto;
    left: auto;
    margin: auto;
  }
}

@media screen and (max-width: 600px) {
  .hs2-dot2-img {
    width: 110px !important;
    height: 100px !important;
    right: 20px !important;
    bottom: -30px !important;
  }
}
@media screen and (max-width: 600px) {
  .hs1_conte {
    margin-left: 10px;
  }
}
